import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";
import { act } from "react";


const token =
"Bearer e159b0cfb0aebe3108daf37c1c41f502b5310e418113b220976d16ceca64b72f833665315d6be6fbb60931ae76634189414efc419b5ac518cbd177754bb4b7598bbaf5617288ecab13eba6b920b7278d59dfed7876efcce67fcd2222eb0f9f47c570270c7edb71772a7e8b3778cb27160418745083210045b15e4bd3a3c1ebd2";

export const fetchProducts = createAsyncThunk("productsSlice/fetchProducts", async () => {
    const response = await fetch(
		'http://localhost:1337/api/products?populate=*',
		{
			method: 'GET',
			headers: {
				"Authorization": "Bearer " + token
			}
		}
	);
    const data = response.json()
    return data
})

const productsSlice = createSlice({
    initialState : [],
    name : "productsSlice",
    reducers : {},
    extraReducers : (builder) => {
        builder.addCase(fetchProducts.fulfilled , (state , action) => {
            return state = action.payload.data
            
        })
    }
})

const {} = productsSlice.actions
export default productsSlice.reducer