import React from "react";
import { Link } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import { decrementAmount, deletItemCart, incrementAmount } from "../rtlk/slices/cart-slices";

function Cart({ setShowCart }) {
  // const [cart, setCart] = useRecoilState(cartState);
  const cart = useSelector(state => state.cart)
  const dispatch = useDispatch();


  // const changAmount = (e, item) => {
  //   const buttonValue = e.target.textContent;
  //   const findPord = cart.find((prod) => {
  //     return prod.id === item.id;
  //   });

  //   if (findPord) {
  //     const newCart = [...cart].map((product) => {
  //       if (product.id === item.id) {
  //         if (buttonValue === "+") {
  //           return (
  //             { ...product, amount: findPord.amount + 1 }
  //           )
  //         } else if (buttonValue === "-" && findPord.amount > 1) {
  //           return { ...product, amount: findPord.amount - 1 };
  //         } else if (buttonValue === "-" && findPord.amount === 1) {
  //           return { ...product, amount: findPord.amount };
  //         }
  //       } else {
  //         return product;
  //       }
  //     });
  //     setCart(newCart);
  //   }
  // };

  const priceTotal = () => {
    const newPrice = []
    let sum = 0;
    cart.map((item) => {
      const price = item.attributes.price * item.amount 
      newPrice.push(price)
      for(let i = 0; i < newPrice.length; i++){
            sum += newPrice[i]
      }
    })
    return sum.toFixed(2)
  }


  const urlapi = "http://localhost:1337";

  return (
    <div
      id="cart"
      className="fixed top-0 right-0 bg-slate-100 w-full lg:w-[460px] h-[580px] z-[1000]"
    >
      <div className="flex items-center justify-between py-[10px] px-[25px]">
        <h3>SHOPPING CART</h3>
        <i
          className="bx bx-x text-[30px]"
          onClick={() => setShowCart(false)}
        ></i>
      </div>
      {/* <div className="grid grid-cols-3 gap-2">
        <div className="text-center px-4 py- border-2 text-[30px] text-[#868686]">
          <i className="bx bxs-bolt"></i>
        </div>
        <div className="text-center px-4 py- border-2 text-[30px] text-[#868686]">
          <i className="bx bx-heart"></i>
        </div>
        <div className="text-center px-4 py- border-2 text-[30px] text-[#868686]">
          <i className="bx bx-cart"></i>
        </div>
      </div> */}
      <div>
        <div className=" flex flex-col  gap-3 h-[530px]  overflow-scroll px-[25px] ">
          {cart.map((item) => {
            return (
              <div className="flex gap-3 border-t-2" key={item.id}>
                <div>
                  <img alt={item.attributes.title}
                    className="w-[170px]"
                    src={urlapi + item.attributes.images.data[0].attributes.url}
                  />
                </div>
                <div className="pt-2 flex flex-col  w-full ">
                  <Link  to={`product/${item.id}`} onClick={() => setShowCart(false) } className="text-[14px] truncate">
                    {item.attributes.title}
                  </Link>
                  <span className="text-[12px] text-[#055507] font-medium">
                    In Stock
                  </span>
                  <div className="flex items-center gap-4">
                    <h1 className="font-medium text-[20px]">
                      ${(item.attributes.price * item.amount).toFixed(2)}
                    </h1>
                    <span className="text-[red] text-[13px] line-through">
                      {item.attributes.discount}
                    </span>
                  </div>
                  <div className="flex flex-col">
                    <span className="text-[10px] font-medium">
                      Category:{" "}
                      <span className="text-[#686868]">
                        {item.attributes.categories.data[0].attributes.name}
                      </span>
                    </span>
                    <span className="text-[10px] font-medium">
                      size:{" "}
                      <span className="text-[#686868]">
                        {/* {item.attributes.sizeclothes === null
                          ? "XL"
                          : item.attributes.sizeclothes[0]} */}
                          {item.sizeTarget}
                      </span>
                    </span>
                  </div>
                  <div className="flex items-center justify-between pt-1">
                    <div className="flex items-center gap-3 bg-slate-200 px-5 py-1">
                      <button onClick={() => dispatch(incrementAmount(item))} >-</button>
                      <span className="border-r-2">{item.amount}</span>
                      <button onClick={() => dispatch(decrementAmount(item))}>+</button>
                    </div>
                    <div className="flex items-center gap-3">
                      <Link onClick={() => setShowCart(false)} to={`product/${item.id}`}><i className='bx bxs-edit-alt text-[20px]' ></i></Link>
                      <i
                        className="bx bx-trash text-[20px] text-[red]"
                        onClick={() => dispatch(deletItemCart(item))}
                      ></i>
                    </div>
                  </div>
                </div>
              </div>
            );
          })}
        </div>
      </div>
      <div className="flex justify-between items-center px-[25px] border-t-2 absolute bottom-0 bg-slate-00 w-full py-2 z-[1000] bg-slate-100 ">
        <div>
          <span className="text-[18px]">Total :</span>
        </div>
        <div>
        <span className="font-medium text-[18px]">${priceTotal()}</span>
        </div>
        <div className="py-1 ">
            <a className="bg-[#C59780] text-white px-[18px] py-[6px] rounded-[20px] text-[12px]">Payer({cart.length})</a>
        </div>
      </div>
    </div>
  );
}

export default Cart;
