import React, { useState } from "react";
// import logo from '../logo_new_black@4x.png';
import Cart from "./Cart";
import { useRecoilState } from "recoil";
import { cartState } from "../atoms/CartAtom";
import { Link } from "react-router-dom";
import { useSelector } from "react-redux";
import logo from "../images/SAFWA@4x.png"

function Header() {
  const [showMenu , setShowMenu] = useState(false)
  const [showCart , setShowCart] = useState(false)

  const cart = useSelector(state => state.cart)

 
  const handlClick = () =>{
    const menu = document.querySelector('#menu')
    setShowMenu(!showMenu)
    if (showMenu === false) {
      menu.classList.add('hidden')
    }else{
      menu.classList.remove('hidden')
    }
  }



  return (
    <header className=" relative ">
      {showCart && <Cart setShowCart={setShowCart}  />}
      <div className="bg-[#F5CAAB] hidden md:block">
        <div className="flex items-center justify-between container mx-auto  py-[10px] ">
          <div className="flex items-center gap-[12px]">
            <div className="flex items-center gap-[5px] transition-all hover:text-[#eeeeee] cursor-pointer ">
              <i className="bx bx-phone text-[20px]"></i>
              <span className="text-[13px]"> +1-202-555-0184</span>
            </div>
            <div className="flex items-center gap-[5px] transition-all hover:text-[#eeeeee] cursor-pointer">
              <i className="bx bx-envelope text-[20px]"></i>
              <span className="text-[13px]">Support@gmail.com</span>
            </div>
          </div>
          <div>
            <ul className="flex items-center gap-[15px]">
              <li>
                <a href="#">
                  <i className="bx bxl-facebook text-[20px]"></i>
                </a>
              </li>
              <li>
                <a href="#">
                  <i className="bx bxl-instagram-alt text-[20px]"></i>
                </a>
              </li>
              <li>
                <a href="#">
                  <i className="bx bxl-gmail text-[20px]"></i>
                </a>
              </li>
              <li>
                <a href="#">
                  <i className="bx bxl-pinterest"></i>
                </a>
              </li>
            </ul>
          </div>
        </div>
      </div>
      <div className="flex items-center justify-between  container mx-auto py-[13px] " >
      <div>
      <i className='bx bx-menu text-[35px] cursor-pointer md:hidden' onClick={handlClick}></i>
      <i className='bx bx-x text-[35px] hidden' onClick={() => setShowMenu(!showMenu)}></i>
      </div>
        <div>
            <Link to='/'>
                <img className="max-w-[100px]" src={logo} />
            </Link>
        </div>
        <div  id="menu" className= "absolute  w-full top-[60px] left-0  z-50 md:relative md:top-0 md:pl-20   "  >
            <ul  className=" flex flex-col items-center gap-[40px] py-[20px] bg-slate-200 w-full h-[300px] md:flex-row md:h-0 md:w-0 md:text-[15px]">
                <li><a className="cursor-pointer transition duration-250 hover:text-blue-500">HOME</a></li>
                <li><a className="cursor-pointer transition duration-250 hover:text-blue-500">SHOP</a></li>
                <li><a className="cursor-pointer transition duration-250 hover:text-blue-500">PRODUCTS</a></li>
                <li><a className="cursor-pointer transition duration-250 hover:text-blue-500">BLOG</a></li>
            </ul>
        </div>
        <div className="pr-[8px] md:pr-0">
          <ul className="flex items-start gap-[12px]">
           <li><a>
           <i className='bx bx-search text-[22px]'></i>
           </a></li>
            <li className=""><a className="relative">
            <i className='bx bx-heart text-[22px] '></i>
            <span className="absolute bottom-3 pr-[5px]">0</span>
            </a></li>
            <li className="">
            <a className="relative">
            <i className='bx bx-cart text-[22px]' onClick={() => setShowCart(true)} ></i>
            <span className="absolute bottom-3 pr-[5px]">{cart.length}</span>
            </a>
            </li>
          </ul>
        </div>
      </div>
    </header>
  );
}

export default Header;
