import { createSlice } from "@reduxjs/toolkit";

const cartSlice = createSlice({
  initialState: [],
  name: " cartSlice",
  reducers: {
    addProducts: (state, action) => {
      const findprod = state.find((item) => item.id === action.payload.id)
      if(findprod){
        findprod.amount = action.payload.amount
        findprod.sizeTarget = action.payload.sizeTarget
      }else {
        state.push({ ...action.payload});
      }
    },
    deletItemCart: (state, action) => {
      return state.filter((item) => item.id !== action.payload.id);
    },
    decrementAmount: (state, action) => {
      const findProd = state.find((item) => item.id === action.payload.id)
      if(findProd){
        findProd.amount += 1
      }
    },
    incrementAmount: (state , action) => {
      const findProd = state.find((item) => item.id === action.payload.id)
      if(findProd){
        findProd.amount -= 1
      }
    }
  },
});

export const { addProducts, deletItemCart, decrementAmount  , incrementAmount} =
  cartSlice.actions;
export default cartSlice.reducer;
