import React from 'react'
import { useRecoilState } from 'recoil';
import { cartState } from '../atoms/CartAtom';
import { Link } from 'react-router-dom';
import { useDispatch } from 'react-redux';
import { addProducts } from '../rtlk/slices/cart-slices';
import AddtoCart from './AddtoCart';
import { showCartsatate } from '../atoms/ShowCart';
import { productsState } from '../atoms/ProductsAtoms';
import { productShowCartsatate } from '../atoms/ProductShowCart';

function ProductCart({product,id}) {

    const [cart , setCart] = useRecoilState(cartState)
    const [showCart , setShowCart] = useRecoilState(showCartsatate)
    const [productShowCart , setProductShowCart] = useRecoilState(productShowCartsatate)

    const dispatch = useDispatch();

    const showAddToCart = (product) => {
      setShowCart(true)
      setProductShowCart(product)

    }

    const urlapi = "http://localhost:1337";
  return (
    <div className="flex flex-col gap-2 box-prod" >
    <div className="relative overflow-hidden "  >
      <img alt={product.attributes.title}
        src={urlapi + product.attributes.images.data[0].attributes.url}
      />
      <div className="absolute top-[10px] right-[10px] cursor-pointer">
        <i className='bx bx-heart text-[23px] text-[#696969] bg-white p-1 rounded-full'></i>
      </div>
      {/* <div id="add-cart" className="absolute bottom-0  lg:block  btn-add-cart " onClick={() => updatCart(product)}>
        <div className="flex items-center bg-[#EBE8E8]  gap-[8px] px-[41px] py-[10px] cursor-pointer">
          <i className="bx bx-cart-add text-[20px] "></i>
          
        </div>
        <div>

        </div>
      </div> */}
      <div className=' hidden absolute bottom-0 w-full md:block  btn-add-cart'>
      <div className='grid grid-cols-2 text-center  text-[20px]  border-2 cursor-pointer '>
        <button onClick={() => showAddToCart(product)} className=' bg-[#EBE8E8]  px-[10px] '><i className='bx bx-cart'></i></button>
        <Link  to={`product/${product.id}`} className='bg-white px-[10px]'><i className='bx bx-show'></i></Link>
      </div>
      </div>
    </div>
    <div className="flex flex-col gap-[5px]">
      <Link to={`/product/${product.id}`} className="text-[13px] truncate">
        {product.attributes.title}
      </Link>
      <div className="flex gap-3 items-center">
        <span className="text-[#696969]">
          ${product.attributes.price}
        </span>
        <span className="text-[red] text-[13px] line-through">
          {product.attributes.discount &&
            `$${product.attributes.discount}`}
        </span>
      </div>
    </div>
    
  </div>
  )
}

export default ProductCart